import React, {
    useRef,
    useState,
    useEffect,
    useMemo,
    memo,
  } from "react";
import {
    Chart,
    LineController,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
  } from "chart.js";

const ChartJS = memo(({props}) => {
    const chartRef = useRef(null);
    let chartInstance = null;
    const [labels, setlabels] = useState([]);
    const [data, setdata] = useState([]);

    useMemo(() => {
        setlabels([]);
        setdata([]);
        props.map((prop)=>{
            setlabels(label =>[...label, prop.name])
            setdata(data => [...data, prop.atk])
        });
      }, [props]);

    useEffect(() => {
        console.log("---ChartJS---")
        const ctx = chartRef.current.getContext("2d");
        const createChart = () => {
        Chart.register(
            LineController,
            CategoryScale,
            LinearScale,
            PointElement,
            LineElement
        );
        chartInstance = new Chart(ctx, {
            type: "line",
            data: {
            labels: labels,
            datasets: [
                {
                label: "Data 1", // ???
                data: data,
                borderColor: "rgba(255, 99, 132, 1)",
                backgroundColor: "rgba(255, 99, 132, 0.2)",
                pointRadius: 5, //  
                pointBackgroundColor: "rgba(255, 99, 132, 1)", //  
                pointBorderColor: "rgba(255, 255, 255, 1)", //   
                pointHoverRadius: 7, //    
                pointHoverBackgroundColor: "rgba(255, 99, 132, 1)", //    
                pointHoverBorderColor: "rgba(255, 255, 255, 1)", //     
                fill: false, //     
                },
            ],
            },
            options: {
                scales: {
                    x: {
                    display: true,
                    },
                    y: {
                    beginAtZero: true,
                    //max: 100, //  
                    },
                },
            },
        });
        };

        const destroyChart = () => {
        if (chartInstance) {
            chartInstance.destroy();
            chartInstance = null;
        }
        };

        const initializeChart = () => {
        destroyChart(); //   
        createChart(); //   
        };

        //      
        initializeChart();

        //  unmount   
        return () => {
        destroyChart();
        };
    }, [props]);

    return <canvas ref={chartRef} />;
  });

  export default ChartJS